.logo {
  width: 230px;
  height: 80px;

  @media (max-width: 1330px) {
    width: 144px;
    height: 50px;
  }
  @media (max-width: 767px) {
    width: 127px;
    height: 44px;
  }
}

//---------------------------- header__icons -------------------------------
.header__icons {
  .header__icon svg {
    fill: var(--text-color-dark);
    width: 40px;
    height: 40px;
    padding: 5px;

    @media (max-width: 1330px) {
      width: 34px;
      height: 34px;
    }
    @media (max-width: 990px) {
      width: 30px;
      height: 30px;
    }
  }
}
.header-home{
  .header__icons {
    .header__icon svg {
      fill: var(--text-color-light);
    }
  }
}

.mobile-menu__close {
  display: flex;
  position: absolute;
  top: 15px;
  right: 20px;
  z-index: 1001;

  svg {
    fill: #fff;
    width: 30px;
    height: 30px;
    padding: 5px;
  }
}

//---------------------------- end header__icons -------------------------------
//---------------------------- News-arrows -------------------------------
.news__prev,
.news__next {
  width: 40px;
  height: 56px;
  fill: var(--primary-color);
  padding: 5px;
  cursor: pointer;

  @media (max-width: 1330px) {
    width: 30px;
    height: 45px;
  }
  @media (max-width: 767px) {
    display: none;
  }
}

//---------------------------- end News-arrows -------------------------------
// ---------------------------- Partners-arrows -------------------------------
.partners__prev,
.partners__next,
.reviews__prev,
.reviews__next {
  width: 40px;
  height: 56px;
  fill: var(--secondary-color);
  padding: 5px;
  cursor: pointer;

  @media (max-width: 1330px) {
    width: 30px;
    height: 45px;
  }
  @media (max-width: 767px) {
    display: none;
  }
}

//---------------------------- end News-arrows -------------------------------
//---------------------------- Read more -------------------------------
.read__more {
  width: 25px;
  height: 16px;
  position: absolute;
  right: -25px;
  top: 50%;
  transform: translateY(-50%);

  @media (max-width: 1330px) {
    width: 18.35px;
    height: 12px;
    right: -18.35px;
  }
  @media (max-width: 767px) {
    width: 15.29px;
    height: 10px;
    right: -15.29px;
  }
}

//---------------------------- end Read more -------------------------------
// ---------------------------- Contacts icons -------------------------------

.contacts__location {
  width: 16px;
  height: 20px;
  fill: #262728;
}

.contacts__phone {
  width: 20px;
  height: 20px;
  fill: #262728;
}

.contacts__email {
  width: 20px;
  height: 16px;
  fill: #262728;
}

// ---------------------------- end Contacts icons -------------------------------